.Header{
    height:65px;
    width:100vw;
    justify-content: space-between;
    background-color: white;
    margin: 0;
    padding: 0;
    display: flex; 
    align-content: center;
    align-items: center;
    box-sizing: border-box;
    position: relative;
    
    -webkit-box-shadow: 1px 2px 8px 2px rgba(57, 63, 72, 0.2);
    -moz-box-shadow: 1px 2px 8px 2px rgba(57, 63, 72, 0.2);
    box-shadow: 1px 2px 8px 2px rgba(57, 63, 72, 0.2);

    @media(max-width:1200px){
     
      width:100%;
      height: 100%;
    }
   
   
}

